* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.span{
  font-size: 40px;
  font-weight: 700;
}

.country-list{
  margin-left: 0;
  padding-left: 0;
  
  
}

li{
  padding-left: 0;
  list-style: none;
}
li>div{
  display: flex;
  align-items: center;
}